.slide-body{
  margin: 0;
  padding: 0;
  display: flex;
  height: 100vh;
  justify-content: center;
  align-items: center;
  background: rgb(57, 247, 247);
  overflow: hidden;
}
img{
  width: 100%;
  height: 100%;
}
.slider{
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.slides{
  width: 340%;
  height: 100%;
  display: flex;
  /* overflow-x: hidden; */
}

.slides input{
  display: none;
}
.slide{
  width: 30%;
  transition: 2s;

}
.slide img{
  width: 100%;
  height: 100%;
}
#opaque-width{
  background-image:url(./garage.png);
