/* css for manual slide navigation */
.navigation-manual{
  position: absolute;
  width: 100%;
  top: 90%;
  display: flex;
  justify-content: center;


}
.manual-btn{
  border: 4px solid rgb(245, 234, 86);
  padding: 1%;
  border-radius: 17px;
  cursor: pointer;
  transition: 1s;
}

.manual-btn:not(:last-child){
  margin-right: 100px;
}

.manual-btn:hover{
  background: rgb(247, 2, 2);
  box-shadow: 2px 2px 2px 2px rgb(245, 234, 86);
}

#radio1:checked ~ .first{
  margin-left: 0;

}
#radio2:checked ~ .first{
  margin-left: -30%;
  
}
#radio3:checked ~ .first{
  margin-left: -60%;
  
}

/* css for automatic navigation */


.navigation-auto{
  position: absolute;
  display: flex;
  width: 100%;
  justify-content: center;
  top: 90%;
}
.navigation-auto div{
  border: 4px solid rgb(250, 222, 63);
  box-shadow: 2px 2px 6px 6px rgb(245, 234, 86);
  padding: 1%;
  border-radius: 18px;
  transition: 1s;
}
.navigation-auto div:not(:last-child){
  margin-right: 100px;
}

#radio1:checked ~ .navigation-auto .auto-btn1{
  background: rgb(247, 2, 2);
}
#radio2:checked ~ .navigation-auto .auto-btn2{
  background: rgb(247, 2, 2);
}
#radio3:checked ~ .navigation-auto .auto-btn3{
  background: rgb(247, 2, 2);
}

#radio1:checked ~ #opaque-width .mymove{
  animation-name: slideinx;
  animation-duration:3s;
  background-color: rgba(240, 248, 255, 0.432);
  width: 38.5%;
  height: 100%;
  left: 0;
}
@keyframes slideinx{
  from{
      width: 0;
  }
  to{
      width: 38.5%;
  }
}
.mymove{
justify-content:space-around;
display: grid;
}
#opaque-width .mymove .b-word{
  font-size: 50px;
  position: absolute;
  width: 20cm;
  padding: 5%;
  visibility: visible;
  color: rgb(209, 3, 3);
  top:4cm;
  /* left: 1cm; */
  /* padding: 10%; */
  overflow: hidden;
}
#opaque-width .mymove .b-word1{
  position: absolute;
  top: 8cm;
  font-size: 50px;
  width: 20cm;
  padding: 5%;
  color: rgb(209, 3, 3);
}
#opaque-width .mymove .b-word2{
  position: absolute;
  top: 12cm;
  font-size: 50px;
  width: 50%;
  padding: 5%;
  color: rgb(209, 3, 3);

}
#radio1:checked ~ #opaque-width .b-word1,#radio1:checked ~ #opaque-width .b-word2{
  visibility: hidden;
}
#radio1:checked ~ #opaque-width .mymove .b-word{

  position: absolute;
  animation-name: within;
  animation-duration:4s;
  width: 50%;
  /* padding: 10%; */
  /* padding-right: 0%; */
  top: 4cm;
  color: rgb(221, 8, 8);
  overflow: hidden;
}
@keyframes within{
  from{
      width: 0%;
      /* position: absolute; */
      /* overflow: hidden; */
  }
  to{
      width: 50%;
      /* position: absolute; */
      /* overflow: hidden; */
      /* right: 5cm; */
  }
}
#radio2:checked ~ #opaque-width .mymove .b-word,#radio2:checked ~ #opaque-width .b-word1,#radio2:checked ~ #opaque-width .b-word2{
  visibility: hidden;
}
#radio2:checked ~ #opaque-width2 .mymove2{
  animation-name: slidein1;
  animation-duration:3s;
  background-color: rgba(240, 248, 255, 0.432);
  width: 38.5%;
  height: 100%;
}
@keyframes slidein1{
  from{
      width: 0;
  }
  to{
      width: 38.5%;
  }
}
#opaque-width2 .mymove2 .b2-word{
  font-size: 50px;
  width: 50%;
  position: absolute;
  visibility: hidden;
  color: rgb(211, 3, 3);
  top: 4cm;
  padding: 5%;
  overflow-x: hidden;
}
#radio2:checked ~ #opaque-width2 .mymove2 .b2-word{
  position: absolute;
  animation-name: within2;
  animation-duration: 7s;
  visibility: visible;
  width: 50%;
  top: 4cm;
}
@keyframes within2{
  from{
      width: 0%;
      /* position: absolute; */
      /* overflow: hidden; */
      visibility: visible;
  }
  to{
      width: 50%;
      /* position: absolute; */
      /* overflow: hidden; */
      visibility: visible;
  }
}
#radio3:checked ~ #opaque-width .mymove .b-word{
  visibility: hidden;
}
#radio3:checked ~ #opaque-width3 .mymove3{
  animation-name: slidein2;
  animation-duration:3s;
  background-color: rgba(240, 248, 255, 0.432);
  width: 38.5%;
  height: 100%;
}
@keyframes slidein2{
  from{
      width: 0;
  }
  to{
      width: 38.5%;
  }
}
#opaque-width3 .mymove3 .b3-word{
  font-size: 50px;
  position: absolute;
  visibility: hidden;
  width: 50%;
  top: 4cm;
  padding: 5%;
  color: rgb(211, 3, 3);
  overflow: hidden;
}
#radio3:checked ~ #opaque-width3 .mymove3 .b3-word{
  position: absolute;
  animation-name: within3;
  animation-duration: 10s;
  visibility: visible;
  width: 50%;
  top: 4cm;
}
@keyframes within3{
  from{
      width: 0%;
      /* position: absolute; */
      overflow: hidden;
      visibility: visible;
  }
  to{
      width: 50%;
      position: absolute;
      overflow: hidden;
      visibility: visible;
  }
}


#radio2:checked ~ #opaque-width .mymove{
  visibility: hidden;
}
.mymove{
  animation-name: slide;
  animation-iteration-count: 1;
  animation-duration: 4s;
  background-color: rgba(240, 248, 255, 0.336);
